import React from 'react'
import Cards from './Cards'
import 'bootstrap/dist/css/bootstrap.min.css';
import Typewriter from 'typewriter-effect';
import { useEffect } from 'react';
import WOW from 'wowjs'

const Projects = ({infos}) => {
  useEffect(() => {
    new WOW.WOW({
      live: false
    }).init();
  }, [])
  return (
    <div className='wrapper'>
      
      <div id='projects' className='my-auto container-fluid px-5'>  
        <div className='text-secondary col-12 typewriter h1 wow'>
          <Typewriter
          options={{
           
            loop: true,
          }}
            onInit={(typewriter) => {
                typewriter.pauseFor(500)
                typewriter.typeString('Projects')         
                .start();
            }}
            />
        </div>
        <div className='projectsText col-12 text-secondary fs-5 mt-3
              wow animate__animated animate__bounceInUp animate__duration-500ms'>This is my very own projects and follow up for more.</div>
        
        <div className='row my-auto d-flex justify-content-start'>     
          {infos.map(el => {
            return(<Cards key={el.id} name={el.name} description={el.description} link={el.link} path={el.path} />)
          })}
        </div>
      </div>  
    </div>
  )
}

export default Projects