import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';
import '../all.min.css';
import '../scss/style.css';


const Nav = () => {
  return (
    <nav className='sticky-top navbar navbar-expand-md navbar-secondary bg-info navbar-static-top'>
        <div className='container-fluid px-5'>
            <a href='#home' className='navbar-brand'></a>
            <button className="navbar-toggler border border-0 border-info " type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <i className="fa-solid fa-bars fs-3"></i>
            </button>

            <div className="collapse navbar-collapse" id="navbarSupportedContent">
                <ul className='navbar-nav ms-auto'>
                    <li className='nav-item ms-2 text-end p-1 m-1'>
                        <a href='#projects' className='projectsBtn nav-link'>Projects</a>
                    </li>
                    <li className='nav-item ms-2 text-end p-1 m-1'>
                        <a href='#skills' className='nav-link'>Skills</a>
                    </li>
                    
                    <li className='nav-item ms-2 text-end p-1 m-1'>
                        <a href='#contactme' className='nav-link'>Contact me</a>
                    </li>
                </ul>
            </div>
        </div>
    </nav>
    
  )
}

export default Nav