import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';

const Cards = ({name,description,link,path}) => {
  return (
    <div className='col-lg-3 col-md-4 col-12  mb-5 mb-lg-0 mt-lg-5 '>
        <div className="card h-100 m-3 shadow wow animate__animated animate__slideInUp animate__duration-500ms">
            <img className="card-img-top" src={path} alt={name}></img>
            <div className="card-body">
                <h5 className="card-title text-secondary">{name}</h5>
                <a href={link} className="nav-link text-secondary"> &#8226; Live Demo</a>
            </div>
        </div>
    </div>
  )
}

export default Cards