import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';
import {useEffect} from 'react';
import '../all.min.css';
import '../scss/style.css';
import 'animate.css';
import Typewriter from 'typewriter-effect';


const Section = () => {
  return (
    <section className='container-fluid px-5 my-5 my-lg-auto'>
        <div className='row text-start'>
            <div className='text-secondary col-12 typewriter h1'>
                <Typewriter
                    onInit={(typewriter) => {
                        typewriter.pauseFor(500)
                        typewriter.typeString('Hello There!')
                        .pauseFor(1700)
                        .deleteAll()
                        typewriter.typeString('I am Wai Yan Min Lwin')
                        .pauseFor(1700)
                        .deleteAll()
                        typewriter.typeString('Enjoy your day!')
                        .start();
                    }}
                />
            </div>
            <div className='col-12 text-secondary bio d-flex flex-column fs-5 mt-3
            animate__animated animate__bounceInDown animate__duration-500ms'>
                <span>This is Wai Yan Min Lwin, a junior web-developer based in Yangon.</span>
                <span>You can explore what I do in my project page! See ya around.</span>
            </div>
            <div className='col-12 mt-3'>
                <a href={require('../assests/resume.pdf')} download className='btn btn-outline-secondary rounded py-1
                 animate__animated animate__bounceInUp animate__duration-500ms'>
                    My Resume
                    <span><i className="ms-2 fa-solid fa-cloud-arrow-down"></i></span>
                </a>
                {/* <button className='ms-2 btn btn-outline-secondary rounded py-1
                animate__animated animate__bounceInRight animate__duration-500ms'>
                    <span className=''>
                        <i className="fa-solid fa-bars p-0"></i>
                    </span>
                    
                </button> */}
                
            </div>
        </div>
    </section>
  )
}

export default Section