import React from 'react'

const Skills = () => {
  return (
    <div className='wrapper container-fluid px-5' id='skills'>
        <h1 className='h1 text-secondary mt-auto wow animate__animated animate__slideInUp animate__duration-500ms'>What Can I do?</h1>
        <div className='row my-auto text-secondary'>
            <div className='col-12 col-md-6 col-lg-4 p-4 shadow rounded bg-danger wow animate__animated animate__slideInUp animate__duration-500ms'>
                <h2 className='h3 web-developement'><i className="fa-solid fa-code mb-2 me-2"></i>Web Developement</h2>
                <p className='fs-6'>Basically, HTML and CSS</p>
                <h3 className='h4' >&#8226; Programming Languages</h3>
                <p className='fs-6'>I can use JavaScript and PHP</p>
                <h3 className='h4' >&#8226; Library and Frameworks</h3>
                <p className='fs-6 lead'>Majority stuffs like Bootstrap, a little bit of React and Laravel.
                A lot of thiry parties like Wow.js, Typewriter-effect and so on
                </p>
            </div>
            <div className='d-none d-md-flex justify-content-center col-md-6 col-lg-8 '>
                <div className='row d-flex justify-content-center align-items-center text-center
                 wow animate__animated animate__bounceInRight animate__duration-500ms'>
                    <i className="fs-1 col-4 html fa-brands fa-html5"></i>
                    <i className="fs-1 col-4 css fa-brands fa-css3-alt animate__animated animate__shakeX animate__faster-500ms animate__infinite"></i>
                    <i className="fs-1 col-4 js fa-brands fa-js animate__animated animate__tada animate__infinite"></i>
                    <i className="fs-1 col-4 bootstrap fa-brands fa-bootstrap"></i>
                    <i className="fs-1 col-4 php fa-brands fa-php animate__animated animate__headShake animate__infinite"></i>
                    <i className="fs-1 col-4 laravel fa-brands fa-laravel animate__animated animate__pulse animate__infinite"></i>
                    <i className="fs-1 col-4 react fa-brands fa-react"></i>
                    <i className="fs-1 col-4 database fa-solid fa-database"></i>
                </div>
                
            </div>
            
        </div>
    </div>
  )
}

export default Skills